import React, { Component } from 'react'
import {NavLink} from 'react-router-dom'
import RouterView from '@/router/routerview';
import {connect} from 'react-redux'
import {bindActionCreators} from 'redux'
import * as navAction from '@/store/nav/nav.action'
import homecss from '@/static/css/home.module.scss';
import { Layout , Menu , Button , Modal} from 'antd';
import Cookie from 'js-cookie'
const { Header, Sider, Content } = Layout;
const { SubMenu } = Menu;
const { confirm } = Modal;
const showConfirm = (that)=>{
    confirm({
        title: '确定要退出登陆吗?',
        content: '退出登陆之后。。。。',
        onOk() {
           //退出登陆
           Cookie.remove('token');
           that.props.history.push('/login');
        }
    });
}
class HomeContainer extends Component {
    render() {
        let {routes,info,navdata,selectedNavData} = this.props;
        console.log(routes);
        return (
            <Layout className={homecss["homeContainer"]}>
                <Sider>
                    <dl className={homecss['userinfo']}>
                        <dt>
                            <img src={info.facePhoto}/>
                        </dt>
                        <dd>
                            {info.phone}
                        </dd>
                    </dl>
                    <Menu
                        mode="inline"
                        theme="dark"
                    >
                        {
                            navdata.map((item,key)=>{
                                return item.children ? <SubMenu
                                    key={key}
                                    title={
                                        <span>
                                            <i className={`iconfont ${item.icon}`}></i>
                                            <span>{item.name}</span>
                                        </span>
                                    }
                                >
                                    {
                                       item.children.map(item=>{
                                           return <Menu.Item key={item.path} onClick={this.addNavTop.bind(this,item.path,item.name)}><NavLink activeClassName={homecss['navactive']} to={item.path}>{item.name}</NavLink></Menu.Item>
                                       }) 
                                    }
                                </SubMenu> :  <Menu.Item key={item.path} onClick={this.addNavTop.bind(this,item.path,item.name)}>
                                    <NavLink  activeClassName={homecss['navactive']} to={item.path}><i className={`iconfont ${item.icon}`}></i>{item.name}</NavLink>
                                </Menu.Item>
                            })
                        }
                    </Menu>
                    <Button onClick={()=>showConfirm(this)}>退出</Button>
                    <Button onClick={()=>this.props.history.push('/home/settings')}>设置</Button>
                </Sider>
                <Layout>
                    <Header className={homecss['navtop']}>
                        {
                            selectedNavData.map(item=><p key={item.path}>
                                <NavLink to={item.path} activeClassName={homecss['active']}>{item.name}</NavLink>
                                <span onClick={this.removeNav.bind(this,item.path)}>&times;</span>
                            </p>)
                        }
                    </Header>
                    <Content>
                        <RouterView routes={routes}/>
                    </Content>
                </Layout>
            </Layout>
        )
    }
    componentDidMount(){
        this.props.initNavData(this.props.location.pathname);
    }
    addNavTop(path,name){
        this.props.addNavData(path,name);
    }
    removeNav(path){
        this.props.removeNavData(path);
    }
}

export default connect(
    state=>{
        return {
            ...state.user,
            ...state.nav
        }
    },
    dispatch=>bindActionCreators(navAction,dispatch)
)(HomeContainer);