import {
    GET_NAV_TOP,
    REMOVE_NAV_TOP,
    INIT_NAV_TOP
} from '../action.type'
export const addNavData = (path,name)=>{
    return {
        type:GET_NAV_TOP,
        path,
        name
    }
}

export const removeNavData = (path)=>{
    return {
        type:REMOVE_NAV_TOP,
        path
    }
}

export const initNavData = (path)=>{
    return {
        type:INIT_NAV_TOP,
        path
    }
}